.wrapper {
  display: inline-block;
  width: 50%;
  height: 2.5rem;
  max-height: 250px;
  position: relative;
  font-size: 0;
  color: #FFF;
  text-align: center;
}
.wrapper_img {
  width: 100%;
  height: 100%;
  background-color: #F7F7F7;
}
.wrapper_img > img {
  width: 100%;
  height: 100%;
}
.wrapper_title {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  padding: 4px;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: rgba(0, 0, 0, 0.3);
}
@media screen and (max-width: 321px) {
  .wrapper_title {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_title {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_title {
    /*大*/
    font-size: 17px;
  }
}
