.wrapper {
  position: relative;
}
.wrapper > img {
  display: block;
  width: 100%;
  height: 3rem;
  max-height: 400px;
}
.wrapper_info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 15px;
  color: #FFF;
  background-color: rgba(0, 0, 0, 0.3);
}
@media screen and (max-width: 321px) {
  .wrapper_info {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info {
    /*大*/
    font-size: 17px;
  }
}
.wrapper_info > li {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
@media screen and (max-width: 321px) {
  .wrapper_info > li:first-child {
    /*小*/
    font-size: 16px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info > li:first-child {
    /*中*/
    font-size: 17px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info > li:first-child {
    /*大*/
    font-size: 19px;
  }
}
@media screen and (max-width: 321px) {
  .wrapper_info > li:nth-child(n+4) {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info > li:nth-child(n+4) {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info > li:nth-child(n+4) {
    /*大*/
    font-size: 15px;
  }
}
