.wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #FFF;
}
.wrapper_container {
  height: 100%;
  overflow-y: auto;
}
.wrapper_container_personal {
  padding: 10px 15px;
}
.wrapper_container_personal_header {
  display: flex;
}
.wrapper_container_personal_header::before {
  content: '';
  width: 4px;
  margin-right: 10px;
  background-color: #FF0000;
}
.wrapper_container_personal_body {
  margin-top: 10px;
}
.wrapper_container_personal_body_item {
  border-radius: 6px;
  width: 32%;
}
.wrapper_container_personal_body_item img {
  border-radius: 6px;
}
